import {
  SET_FOOTER_LINK
} from "../../constants/Footer/footer";

const initial_state = {
  linkFooter: false,
};

export default function reducer(state = initial_state, action) {
  switch (action.type) {
    case SET_FOOTER_LINK:
      return state = { ...state, linkFooter: action.payload, };
    default:
      return state;
  }
}
