import {
    SET_DEVICE,
} from "../../constants/screenSize/screenSize"

const initial_state = {
    device:((window.innerWidth <720) && "small") || ((window.innerWidth>=720 && window.innerWidth <= 900) && "medium") || (window.innerWidth>900 && "large")
  };


  export default function reducer(state = initial_state, action){
    switch (action.type) {
        
      case SET_DEVICE:
        console.log("router",window.innerWidth);
            if (window.innerWidth < 720) {
                return (state = { ...state, device:"small"  });
            }if(window.innerWidth>=720 && window.innerWidth < 960) {
                return (state = { ...state, device:"medium"  });
            }if(window.innerWidth>=960){
                return (state = { ...state, device:"large"  });
            }
            break;
        default:
        return state;
    }
  }
